import { clamp } from 'gsap/gsap-core';
import * as THREE from 'three'
import { AdditiveBlending, NormalBlending } from 'three';
import * as script from '../script.js';

var mesh, clock, cubeSineDriver,
    smokeMaterial, smokeGeo, smokeParticles, delta, smokeParticlesPositions;


let galaxyColors = [
    new THREE.Color("#878787").multiplyScalar(2.5),
    new THREE.Color("#3b3b3b").multiplyScalar(2.5),
    new THREE.Color("#171717").multiplyScalar(2.5),
    new THREE.Color("#000000").multiplyScalar(2.5)
];

export function init(scene) {
    clock = new THREE.Clock();



    smokeGeo = new THREE.PlaneGeometry(3, 3);
    smokeParticles = [];
    smokeParticlesPositions = [];

    for (var p = 0; p < 100; p++) {
        smokeMaterial = new THREE.MeshLambertMaterial
            ({
                color: galaxyColors[Math.floor(Math.random() * galaxyColors.length)],
                map: new THREE.TextureLoader().
                    load("https://iili.io/bzbRnt.png"),
                opacity: .3,
                blending: NormalBlending,
                depthWrite: false,
                depthTest: false,
                transparent: true,
                fog: true
            });

        var particle = new THREE.Mesh(smokeGeo, smokeMaterial);
        var radius = 6;
        particle.position.set(Math.random() * radius - radius / 2, Math.random() * radius, Math.random() * radius - radius * 1.5);
        particle.rotation.z = Math.random() * 360;
        var randomscale = Math.random() * 2 + .5;
        particle.scale.set(randomscale, randomscale, randomscale);
        scene.add(particle);
        smokeParticlesPositions.push(particle.position.x)
        smokeParticles.push(particle);
    }
    animate();

}

function animate() {
    delta = clock.getDelta();
    requestAnimationFrame(animate);
    evolveSmoke();
}


function evolveSmoke() {
    if (script.bodyScrollBar.offset.y > 2000)
        return;
    var sp = smokeParticles.length;
    while (sp--) {
        var value = clamp(-1.5, 1.5, (script.bodyScrollBar.offset.y - 1000) * .002 * smokeParticlesPositions[sp]);

        smokeParticles[sp].rotation.z += (delta * 0.13);
        smokeParticles[sp].rotation.z += (.3 - smokeParticles[sp].material.opacity) * .02 * value;
        if (script.bodyScrollBar.offset.y > 1000) {
            smokeParticles[sp].position.x = smokeParticlesPositions[sp]
                + (value * value * value);
            smokeParticles[sp].material.opacity = (1 - (script.bodyScrollBar.offset.y - 1000) * .001) * .3;
        }


    }

}



