renderer.physicallyCorrectLights = true;

/**
 * BackGroundEffects
 */
Smoke.init(scene);



new RGBELoader()
    .setDataType(THREE.UnsignedByteType)
    .setPath('https://threejs.org/examples/textures/equirectangular/')
    .load('royal_esplanade_1k.hdr', function (texture) {

        var envMap = pmremGenerator.fromEquirectangular(texture).texture;

        //scene.background = envMap;
        scene.environment = envMap;

        texture.dispose();
        pmremGenerator.dispose();
    });

var pmremGenerator = new THREE.PMREMGenerator(renderer);
pmremGenerator.compileEquirectangularShader();


// ----------- Instantiate a loader
const loader = new GLTFLoader();

// Optional: Provide a DRACOLoader instance to decode compressed mesh data
const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath('three/examples/js/libs/draco/');
loader.setDRACOLoader(dracoLoader);


// Load a glTF resource
loader.load(
    // resource URL
    'https://echend.github.io/delco/gltf/gltf.gltf',
    // called when the resource is loaded
    function (gltf) {
        scene.add(gltf.scene);
        console.log(gltf.scene);
        gltf.animations; // Array<THREE.AnimationClip>
        gltf.scene; // THREE.Group
        gltf.scenes; // Array<THREE.Group>
        gltf.cameras; // Array<THREE.Camera>
        gltf.asset; // Object


        gltf.scene.children[1].children.forEach(element => {
            if (element.name == "Delco_00_(1)") {
                element.children.forEach(element => {
                    element.frustumCulled = false;
                });
            }
        });



        camera = gltf.cameras[0];
        //camera.fov = 45.0;
        groupRotation = gltf.scene.children[1];

        UpdateResizing();
        mixer = new THREE.AnimationMixer(gltf.scene)
        var action = mixer.clipAction(gltf.animations[0]);
        mixer.setTime(0);
        createAnimation(mixer, action, gltf.animations[0]);
        Loaded = true;
    },
    // called while loading is progressing
    function (xhr) {

        console.log((xhr.loaded / xhr.total * 100) + '% loaded');

    },
    // called when loading has errors
    function (error) {

        console.log('An error happened');
        console.log(error);
    }
);




function createAnimation(mixer, action, clip) {
    let proxy = {
        get time() {
            return mixer.time;
        },
        set time(value) {
            action.paused = false;
            mixer.setTime(value);
            action.paused = true;
        }
    };

    let scrollingTL = gsap.timeline({
        scrollTrigger: {
            trigger: renderer.domElement,
            start: "top top",
            end: "+=20000",
            pin: true,
            markers: false,
            invalidateOnRefresh: true,
            scrub: 3,
            onUpdate: function () {
                camera.updateProjectionMatrix();
            },
            onEnter: function () {
                mixer.setTime(0);
                action.play();
            },
            toggleActions: "restart complete reverse reset",
        }
    });

    scrollingTL.to(proxy, {
        time: clip.duration,
        repeat: 0
    });

}


function fadeIn(el) {
    let tl = gsap.timeline()
        .to(el, { autoAlpha: 1, duration: .5 }, "<")
    return tl;
}
function fadeOut(el) {
    let tl = gsap.timeline()
        .to(el, { autoAlpha: 0, duration: .5 }, ">")
    return tl;
}


function SetupSceneAnimation(index, length, TextCount) {
    function animationOne() {
        let tl = gsap.timeline()
            .add(fadeOut('.header.S_0' + index + '_00'), ">1")
        for (let i = 1; i < TextCount - 2; i++) {
            tl
                .add(fadeIn('.header.S_0' + index + '_0' + i), ">.5")
                .add(fadeOut('.header.S_0' + index + '_0' + i), ">1")
        }

        return tl;
    }
    let tl = gsap.timeline()
        .add(animationOne(), ">-1")
        .add(fadeIn('.header.S_0' + index + '_0' + (TextCount - 2)), ">2")
        .add(fadeOut('.header.S_0' + index + '_0' + (TextCount - 2)), ">1")
        .add(fadeIn('.header.S_0' + index + '_0' + (TextCount - 1)), ">1")
        .add(fadeOut('.header.S_0' + index + '_0' + (TextCount - 1)), ">2.5")
        .set({}, {}, "+=4")

    ScrollTrigger.create({
        scrub: 3,
        markers: false,
        trigger: '#Scene_0' + index,
        start: 'top top',
        end: '+=' + length,
        id: "main",
        animation: tl,
        pin: false,
        toggleActions: "restart complete reverse reset",
    });
}

